import React from "react";
import BundleTitle from "./bundle/BundleTitle";
import BundleContent from "./bundle/BundleContent";
import BundleButton from "./bundle/BundleButton";
import "../styles/bundle.css";
import bundleIcon from "../icons/pictures/data-center.png";

const Bundle = () => {
  return (
    <div className="bundle-container">
      <div className="bundle-image">
        <img src={bundleIcon} alt="Bundle Icons" className="bundleIcon" />
      </div>

      <div className="bundle-sub">
        <BundleTitle title="Protect your remote workers with a free trial of Cisco’s integrated security solution " />
        <BundleContent
          paragraph_one="See how Cisco Secure Remote Worker combines the capabilities of Secure Endpoint, Umbrella, Duo, and more in an integrated solution. "
          paragraph_two="Offer expires July 31, 2021"
        />
        <BundleButton />
      </div>
    </div>
  );
};

export default Bundle;



