import React from "react";
import "../styles/marquee.css";
import marqueeImage from "../icons/image/marquee_image.svg";

function Marquee() {
  return (
    <div className="marquee-container">
      <img className="marquee-image" src={marqueeImage} alt="marquee" />
      <div className="marquee-card">
        <span className="marquee-card-subtitle">
          Cisco Secure Endpoint (AMP for Endpoints)
        </span>
        <p className="marquee-card-title">
          Fortify your endpoints with unified security
        </p>
        <p className="marquee-card-content">
          Coordinate your defense across all users and devices. Secure Endpoint
          combines protection, detection and response in a cloud-native solution
          that cuts remediation times up to 85 percent.
        </p>
        <button className="marquee-card-button">Request 1:1 demo</button>
      </div>
    </div>
  );
}

export default Marquee;



