import React from "react";
import Star from "./Stars";
import GreaterThan from "../../icons/12/greater_than.svg";
import TrustRadius from "../../icons/info/TrustRadius-Logo.svg";

function Card({ review, isActive }) {
  return (
    // <div className="card-container-outer">
    <div className={isActive ? "carousel-cell" : "carousel-cell-adjust"}>
      <div className="card-star-container">
        <Star rate={review.rating} />
      </div>
      <p className="card-content">{review.content}</p>
      <div className="card-body-review">
        <p className="card-body">{review.body}</p>
        <div className="card-cta-container">
          <div className="text">Read full review</div>
          <div className="icon">
            <img src={GreaterThan} alt="Greater Than" />
          </div>
        </div>
      </div>
      <div className="card-trust-review">
        <div className="card-trust-radius">
          <p className="trust-radius-text">powered by</p>
          <img src={TrustRadius} alt="TrustRadius" className="TrustRadius" />
        </div>
        <div className="card-review">
          <div className="view-reviews">View TrustRadius Reviews</div>
          <p className="write-review">Write a Review</p>
        </div>
      </div>
    </div>
    // </div>
  );
}

export default Card;



