import React from "react";
import GreaterThan from "../../icons/12/greater_than.svg";

const ViewDoc = ({ viewOption }) => {
  return (
    <div className="doc-option-container">
      <div className="option">{viewOption}</div>
      <div className="icon-invest">
        <img src={GreaterThan} alt="Greater Than" />
      </div>
    </div>
  );
};

export default ViewDoc;



