import React from "react";
import fullStar from "../../icons/stars/full.svg";
import halfStar from "../../icons/stars/half.svg";
import emptyStar from "../../icons/stars/empty.svg";

function Stars({ rate }) {
  switch (rate) {
    case 0.5:
      return (
        <span className="star">
          <img src={halfStar} alt="Half Star" />
          <img src={emptyStar} alt="Empty Star" />
          <img src={emptyStar} alt="Empty Star" />
          <img src={emptyStar} alt="Empty Star" />
          <img src={emptyStar} alt="Empty Star" />
        </span>
      );
    case 1:
      return (
        <span className="star">
          <img src={fullStar} alt="Full Star" />
          <img src={emptyStar} alt="Empty Star" />
          <img src={emptyStar} alt="Empty Star" />
          <img src={emptyStar} alt="Empty Star" />
          <img src={emptyStar} alt="Empty Star" />
        </span>
      );
    case 1.5:
      return (
        <span className="star">
          <img src={fullStar} alt="Full Star" />
          <img src={halfStar} alt="Half Star" />
          <img src={emptyStar} alt="Empty Star" />
          <img src={emptyStar} alt="Empty Star" />
          <img src={emptyStar} alt="Empty Star" />
        </span>
      );
    case 2:
      return (
        <span className="star">
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={emptyStar} alt="Empty Star" />
          <img src={emptyStar} alt="Empty Star" />
          <img src={emptyStar} alt="Empty Star" />
        </span>
      );
    case 2.5:
      return (
        <span className="star">
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={halfStar} alt="Half Star" />
          <img src={emptyStar} alt="Empty Star" />
          <img src={emptyStar} alt="Empty Star" />
        </span>
      );
    case 3:
      return (
        <span className="star">
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={emptyStar} alt="Empty Star" />
          <img src={emptyStar} alt="Empty Star" />
        </span>
      );
    case 3.5:
      return (
        <span className="star">
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={halfStar} alt="Half Star" />
          <img src={emptyStar} alt="Empty Star" />
        </span>
      );
    case 4:
      return (
        <span className="star">
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={emptyStar} alt="Empty Star" />
        </span>
      );
    case 4.5:
      return (
        <span className="star">
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={halfStar} alt="Half Star" />
        </span>
      );
    default:
      return (
        <span className="star">
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
          <img src={fullStar} alt="Full Star" />
        </span>
      );
  }
}

export default Stars;



