import React from "react";
import DocIcon from "./DocIcon";

const Divide = () => {
  return (
    <div className="divide">
      <div className="vertical-line-up"></div>
      <DocIcon />
      <div className="vertical-line-bottom"></div>
    </div>
  );
};

export default Divide;



