import React from "react";
import "../styles/demo.css";
import demo from "../icons/LTDemo/demo.svg";
import DemoCard from "./LTDemo/DemoCard";

const title = "Cisco Intersight with a \nhands-on demo";

const content =
  "It's your secure place for connecting and getting things done: call, message, and meet, all in one app. Body copy 224 characters max; 63 character line limit, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut.";

const button = "Request a demo";

function LTDemo() {
  return (
    <div className="demo-container">
      <img className="demo-image" src={demo} alt="demo" />
      <DemoCard title={title} content={content} button={button} />
    </div>
  );
}

export default LTDemo;



