import React from "react";
import AppTitle from "./appCenter/AppTitle";
import AppContent from "./appCenter/AppContent";
import AppButton from "./appCenter/AppButton";
import "../styles/appCenter.css";
import appIcon from "../icons/pictures/app.png";

const AppCenter = () => {
  return (
    <div className="app-container">
      <div className="app-container-sub">
        <div className="app-sub">
          <AppTitle title="Headline 70 characters max... Lorem ipsum dolor sit amet, consectetur elit" />
          <AppContent paragraph="Body copy paragraph 100 characters max; Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nec, sapien ferrmentum in ante. Vitae ut lectus non gravida" />
          <AppButton />
        </div>
      </div>

      <img src={appIcon} alt="App Icons" className="appIcon" />
    </div>
  );
};

export default AppCenter;



