import React, { useState, useCallback, useEffect } from "react";
import Ratings from "./carousel/Ratings";
import Card from "./carousel/Card";
import "../styles/carousel.css";
import styled from "styled-components";

const useMediaQuery = (width) => {
  const [targetReached, setTargetReached] = useState(false);

  const updateTarget = useCallback((e) => {
    if (e.matches) {
      setTargetReached(true);
    } else {
      setTargetReached(false);
    }
  }, []);

  useEffect(() => {
    const media = window.matchMedia(`(min-width: ${width}px)`);
    media.addEventListener("change", (e) => updateTarget(e));

    // Check on mount (callback is not called until a change occurs)
    if (media.matches) {
      setTargetReached(true);
    }

    return () => media.removeEventListener("change", (e) => updateTarget(e));
  }, [updateTarget, width]);

  return targetReached;
};

const reviews = [
  {
    rating: 4.5,
    content:
      "Card 1 title 150 characters max, 6 cards max duis autem vel eum dolor in hendrerit in vulputate. Lorem ipsum dolor sit amet...",
    body: "Body copy bold 30 characters max",
  },
  {
    rating: 3.5,
    content:
      "Card 2 title 150 characters max, 6 cards max duis autem vel eum dolor in hendrerit in vulputate. Lorem ipsum dolor sit amet...",
    body: "Body copy bold 30 characters max",
  },
  {
    rating: 4,
    content:
      "Card 3 title 150 characters max, 6 cards max duis autem vel eum dolor in hendrerit in vulputate. Lorem ipsum dolor sit amet...",
    body: "Body copy bold 30 characters max",
  },
];

function Carousel() {
  const isDesktopXL = useMediaQuery(1920);
  const isDesktop = useMediaQuery(1440);
  const isTablet = useMediaQuery(768);
  const isMobile = useMediaQuery(320);
  let cardWidth;

  if (isDesktopXL) {
    cardWidth = 55.66;
  } else if (isDesktop) {
    cardWidth = 73.84;
  } else if (isTablet) {
    cardWidth = 85.44;
  } else if (isMobile) {
    cardWidth = 90;
  }

  const [cardIndex, setCardIndex] = useState(0);

  const Span = styled.span`
    & {
      height: 8px;
      width: 8px;
      display: flex;
      align-items: center;
      cursor: pointer;
    }

    &::before {
      content: "";
      height: 6px;
      width: 6px;
      background-color: #c3c4c5;
      border-radius: 20px;
      transition: background-color 0.3s ease;
    }

    &:hover::before {
      background-color: #54bbe6;
    }

    &[data-dot="${cardIndex}"]::before {
      background-color: #54bbe6;
    }
  `;

  const onPrev = () => {
    if (cardIndex > 0) {
      setCardIndex((prevIndex) => prevIndex - 1);
    } else {
      setCardIndex(0);
    }
  };

  const onNext = () => {
    if (cardIndex < reviews.length - 1) {
      setCardIndex((nextIndex) => nextIndex + 1);
    } else {
      setCardIndex(reviews.length - 1);
    }
  };

  const trackClass = {
    transition: "transform 0.6s cubic-bezier(0.42, 0, 0.58, 1) 0s",
    transform: `translateX(${-cardWidth * cardIndex}vw)`,
  };

  const handleClick = (e) => {
    let value = Number(e.target.getAttribute("data-dot"));
    setCardIndex(value);
  };

  return (
    <div className="carousel-container">
      <div className="carousel-header">
        <header className="headline">Hear from your peers</header>
        <Ratings
          review={33}
          isDesktopXL={isDesktopXL}
          isDesktop={isDesktop}
          isTablet={isTablet}
          isMobile={isTablet}
        />
      </div>
      <div className="carousel-card">
        <div className="prevCard" onClick={onPrev}></div>
        <div className="nextCard" onClick={onNext}></div>
        <div style={trackClass}>
          <ul>
            {reviews.map((review, index) => {
              let isActive = index === cardIndex;
              return (
                <li key={index}>
                  <Card review={review} isActive={isActive} />
                </li>
              );
            })}
          </ul>
        </div>
        <div className="rectangle-container">
          {reviews.map((_, i) => {
            return (
              <Span key={i} data-dot={i} onClick={(e) => handleClick(e)} />
            );
          })}
        </div>
      </div>
    </div>
  );
}

export default Carousel;



