import React, { useState, useCallback, useEffect, Fragment } from "react";
import "../styles/footer.css";
import Main from "./footer/Main";
import SoMe from "./footer/SoMe";
import Sub from "./footer/Sub";
import CopyRight from "./footer/CopyRight";
import Accordion from "./footer/Accordion";

const useMediaQuery = (width) => {
  const [targetReached, setTargetReached] = useState(false);

  const updateTarget = useCallback((e) => {
    if (e.matches) {
      setTargetReached(true);
    } else {
      setTargetReached(false);
    }
  }, []);

  useEffect(() => {
    const media = window.matchMedia(`(min-width: ${width}px)`);
    media.addEventListener("change", (e) => updateTarget(e));

    // Check on mount (callback is not called until a change occurs)
    if (media.matches) {
      setTargetReached(true);
    }

    return () => media.removeEventListener("change", (e) => updateTarget(e));
  }, [updateTarget, width]);

  return targetReached;
};

const links = ["About Us", "Product", "Solutions", "Support", "Learn"];
const contact = ["Feedback", "Help"];
const legal = [
  "Sitemap",
  "Terms & Conditions",
  "Statement",
  "Cookies",
  "Trademarks",
];

function Footer() {
  const isDesktop = useMediaQuery(1440);
  const isDesktopMD = useMediaQuery(1024);
  const isTablet = useMediaQuery(768);
  const isMobile = useMediaQuery(320);

  let display;

  if (isDesktop) {
    display = (
      <div className="footer-container">
        <div className="upper-container">
          <Main />
          <SoMe />
        </div>
        <div className="lower-container">
          <Sub />
          <CopyRight />
        </div>
      </div>
    );
  } else if (isDesktopMD) {
    display = (
      <div className="footer-container">
        <Main />
        <Sub />
        <SoMe />
        <CopyRight />
      </div>
    );
  } else if (isTablet || isMobile) {
    display = (
      <div className="footer-container">
        <Fragment>
          <Accordion title="Quick links" content={links} />
          <Accordion title="Contact" content={contact} />
          <Accordion title="Legal" content={legal} />
        </Fragment>

        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <SoMe />
          <CopyRight />
        </div>
      </div>
    );
  }

  return <Fragment>{display}</Fragment>;
}

export default Footer;



