import React, {
  useState,
  useCallback,
  useEffect,
  useRef,
  Fragment,
} from "react";
import "../styles/features.css";
import FeatureAccordion from "./features/FeatureAccordion";
import FeatureCarousel from "./features/FeatureCarousel";
import GreaterThan from "../icons/12/greater_than.svg";
import imageOne from "../icons/featuresAccordion/1.png";
import imageTwo from "../icons/featuresAccordion/2.png";
import imageThree from "../icons/featuresAccordion/3.png";
import imageFour from "../icons/featuresAccordion/4.png";
import UseFadeIn from "./features/UseFadeIn";

const featureAccordion = [
  {
    id: 1,
    title: "Comprehensive XDR and EDR capabilities",
    content:
      "Tackle cyberthreats on two fronts. With access to the built-in SecureX platform, Secure Endpoint gives you both EDR (endpoint detection and response) for rapid remediation and XDR (extended detection and response) for simplified incident management.",
    image: imageOne,
  },
  {
    id: 2,
    title: "Dynamic search technology",
    content:
      "Keep a closer eye on your endpoints. Using the Orbital Advanced Search capability, Secure Endpoint is quick to deliver deep insights on the state of your devices, simplifying threat hunting with hundreds of pre-canned, customizable queries at your fingertips.",
    image: imageTwo,
  },
  {
    id: 3,
    title: "Always-on threat hunting services",
    content:
      "Add proactive threat hunting to your SecOps arsenal. The cloud-native SecureX platform includes a team of experts who work for you around the clock to track sophisticated threats to your network and map out how to respond to vulnerabilities.",
    image: imageThree,
  },
  {
    id: 4,
    title: "TK malware analysis",
    content:
      "Take an active approach to endpoint security. With a highly secure sandboxing environment, Secure Endpoint lets you dig through the details of suspicious files, assess the severity of risks, and build the knowledge you need to undermine malware and block future attacks.",
    image: imageFour,
  },
];

const useMediaQuery = (width) => {
  const [targetReached, setTargetReached] = useState(false);

  const updateTarget = useCallback((e) => {
    if (e.matches) {
      setTargetReached(true);
    } else {
      setTargetReached(false);
    }
  }, []);

  useEffect(() => {
    const media = window.matchMedia(`(min-width: ${width}px)`);
    media.addEventListener("change", (e) => updateTarget(e));

    // Check on mount (callback is not called until a change occurs)
    if (media.matches) {
      setTargetReached(true);
    }

    return () => media.removeEventListener("change", (e) => updateTarget(e));
  }, [updateTarget, width]);

  return targetReached;
};

function Features() {
  const { isVisible, element } = UseFadeIn(180);

  const isDesktop = useMediaQuery(1440);
  const isTablet = useMediaQuery(768);
  const isMobile = useMediaQuery(320);

  const [seletedAccodion, setSeletedAccodion] = useState(1);
  const [isClick, setIsClick] = useState(false);

  const handleClick = (id) => {
    setSeletedAccodion(id);
  };

  // handle autoply
  // const [isPlay, setIsPlay] = useState(true);
  const delay = 5000;
  const timeoutRef = useRef(null);
  const resetTimeout = () => {
    if (!timeoutRef.current) {
      return;
    } else {
      clearTimeout(timeoutRef.current);
    }
  };

  useEffect(() => {
    resetTimeout(timeoutRef.current);
    // timeoutRef.current = setTimeout(() => {
    if (isVisible && !isClick) {
      const intervalId = setInterval(() => {
        setSeletedAccodion((prevId) =>
          prevId === featureAccordion.length ? 1 : prevId + 1
        );
      }, delay);

      return () => {
        // clearTimeout(timeoutRef.current);
        clearInterval(intervalId);
      };
    } else {
      return;
    }
  }, [seletedAccodion, isVisible]);

  let accordionDisplay = (
    <Fragment
    // onMouseOver={() => setIsPlay(false)}
    // onMouseOut={() => setIsPlay(true)}
    >
      {featureAccordion.map((accordion) => {
        return (
          <FeatureAccordion
            accordion={accordion}
            id={accordion.id}
            open={accordion.id === seletedAccodion}
            onClick={handleClick}
            key={accordion.id}
            screen={!isDesktop}
            isVisible={isVisible}
            isClick={isClick}
            setIsClick={setIsClick}
          />
        );
      })}
    </Fragment>
  );

  let secondaryCTA = (
    <div className="secondaryCTA-launch">
      <div className="secondaryCTA-button">Secondary CTA</div>
      <div className="secondaryCTA-icon-greater">
        <img src={GreaterThan} alt="Greater Than" />
      </div>
    </div>
  );

  let carouselDisplay = (
    <Fragment>
      {featureAccordion.map((accordion, i) => (
        <Fragment>
          {seletedAccodion === i + 1 && (
            <FeatureCarousel renderImage={accordion.image} />
          )}
        </Fragment>
      ))}
    </Fragment>
  );

  let display;
  if (isDesktop) {
    display = (
      <div className="feature-container">
        <div className="feature-container-left">
          <div ref={element} className="feature-headline">
            Hold the upper hand over cyberthreats
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              margin: "0",
              padding: "0",
            }}
            className={isVisible ? " is-visible" : "not-visible"}
          >
            <div className="accordion-total-height">{accordionDisplay}</div>
            {secondaryCTA}
          </div>
        </div>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            margin: "0",
            padding: "0",
          }}
          className={isVisible ? " is-visible" : "not-visible"}
        >
          <div className="feature-container-right">{carouselDisplay}</div>
        </div>
      </div>
    );
  } else if (isTablet || isMobile) {
    display = (
      <div className="feature-container">
        <div ref={element} className="feature-headline">
          Hold the upper hand over cyberthreats
        </div>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            margin: "0",
            padding: "0",
          }}
          className={isVisible ? " is-visible" : "not-visible"}
        >
          {accordionDisplay}
          {secondaryCTA}
        </div>
      </div>
    );
  }

  return <Fragment>{display}</Fragment>;
}

export default Features;



