import React from "react";
import "../styles/webex.css";

function Webex() {
  return (
    <div className="webex-conainer">
      <header className="webex-header">Endcap headline</header>
      <div className="webex-body">
        <p className="webex-content">
          Webex is your one easy-to-use and secure app to call, message, meet
          and get work done. Additional features and support start at $13.50.
        </p>
        <button className="webex-button">Request a demo</button>
      </div>
    </div>
  );
}

export default Webex;



