import React from "react";
import GreaterThan from "../../icons/12/greater_than.svg";
import "../..//styles/demo.css";

function DemoCard({ title, content, button }) {
  return (
    <div className="democard">
      <div className="democard-title">{title}</div>
      <p className="democard-content">{content}</p>
      <div className="democard-launch">
        <div className="democard-button">{button}</div>
        <div className="democard-icon-greater">
          <img src={GreaterThan} alt="Greater Than" />
        </div>
      </div>
    </div>
  );
}

export default DemoCard;



