import React from "react";
import GreaterThan from "../../icons/12/greater_than.svg";

const CollabCard = ({ subtitle, content, viewOption }) => {
  return (
    <div className="collab-card">
      <div className="collab-card-subtitle">{subtitle}</div>
      <p className="collab-card-content">{content}</p>
      <div className="collab-card-viewoption">
        <div className="option">{viewOption}</div>
        <div className="icon-greater">
          <img src={GreaterThan} alt="Greater Than" />
        </div>
      </div>
    </div>
  );
};

export default CollabCard;



