import React, { Fragment } from "react";
import "../../styles/licensing.css";
import Smartphone from "../../icons/phone/smartphone.svg";

function LicensingCardFour({ isDesktop, isTablet, isMobile }) {
  let display;
  if (isDesktop) {
    display = (
      <div className="licensing-card-container">
        <div className="licensing-tag"></div>
        <div className="licensing-card-subhead">Cisco DNA</div>
        <div className="licensing-card-head">Premier</div>
        <div className="licensing-card-price">
          <div className="price">
            <div className="price-divider"></div>
            <div className="price-container">
              <div className="price-call-container">
                <div className="price-smartphone">
                  <img src={Smartphone} alt="smartphone" />
                </div>
                <div className="price-call">Call for price</div>
              </div>
            </div>
          </div>
          <button className="price-btn">Get started</button>
        </div>
        <p className="licensing-card-content">
          Recommended for multiple data centers and/or clouds. Provides the
          highest innovations and includes all Data Center Networking Advantages
          features, plus Day 2 Operations Suite.
        </p>
      </div>
    );
  } else if (isTablet) {
    display = (
      <div className="licensing-card-container">
        <div className="licensing-card-container-top"></div>
        <div className="licensing-card-container-bottom">
          <div className="licensing-card-container-left">
            <div className="licensing-card-subhead">Cisco DNA</div>
            <div className="licensing-card-head">Premier</div>
            <p className="licensing-card-content">
              Recommended for multiple data centers and/or clouds. Provides the
              highest innovations and includes all Data Center Networking
              Advantages features, plus Day 2 Operations Suite.
            </p>
          </div>
          <div className="licensing-card-container-right">
            <div className="licensing-card-price">
              <div className="price">
                <div className="price-divider"></div>
                <div className="price-container">
                  <div className="price-call-container">
                    <div className="price-smartphone">
                      <img src={Smartphone} alt="smartphone" />
                    </div>
                    <div className="price-call">Call for price</div>
                  </div>
                </div>
              </div>
              <button className="price-btn">Get started</button>
            </div>
          </div>
        </div>
      </div>
    );
  } else if (isMobile) {
    display = (
      <div className="licensing-card-container">
        <div className="licensing-card-subhead">Cisco DNA</div>
        <div className="licensing-card-head">Premier</div>
        <div className="licensing-card-price">
          <div className="price">
            <div className="price-container">
              <div className="price-call-container">
                <div className="price-smartphone">
                  <img src={Smartphone} alt="smartphone" />
                </div>
                <div className="price-call">Call for price</div>
              </div>
            </div>
          </div>
          <button className="price-btn">Get started</button>
        </div>
        <p className="licensing-card-content">
          Recommended for multiple data centers and/or clouds. Provides the
          highest innovations and includes all Data Center Networking Advantages
          features, plus Day 2 Operations Suite.
        </p>
      </div>
    );
  }

  return <Fragment>{display}</Fragment>;
}

export default LicensingCardFour;



