import React from "react";
import "../../styles/footer.css";
import styled from "styled-components";

const Span = styled.span`
  margin-right: 32px;
`;

function Main() {
  return (
    <div className="main">
      <Span>About Us</Span>
      <Span>Product</Span>
      <Span>Solutions</Span>
      <Span>Support</Span>
      <Span>Learn</Span>
    </div>
  );
}

export default Main;



