import React, { Fragment } from "react";
import "../../styles/licensing.css";
import Dollar from "../../icons/currency/USDollar.svg";

function LicensingCardTwo({ isDesktop, isTablet, isMobile }) {
  let display;
  if (isDesktop) {
    display = (
      <div className="licensing-card-container">
        <div className="licensing-tag">
          <div className="licensing-tag-tier">Tier One tag; 20 char</div>
        </div>
        <div className="licensing-card-subhead">Cisco DNA</div>
        <div className="licensing-card-head">Essentials</div>
        <div className="licensing-card-price">
          <div className="price">
            <div className="price-divider"></div>
            <div className="price-container">
              <div className="price-dollar">
                <img src={Dollar} alt="dollar" />
              </div>
              <div className="price-integer">14</div>
              <div className="price-right">
                <div className="price-decimal">95</div>
                <div className="price-period">/month</div>
              </div>
            </div>
          </div>
          <button className="price-btn">Get Started</button>
        </div>
        <p className="licensing-card-content">
          Recommmended for single data centers. Includes both NX-OS and ACI
          management and operations capabilities, offering flexibility to
          migrate as needed.
        </p>
      </div>
    );
  } else if (isTablet) {
    display = (
      <div className="licensing-card-container">
        <div className="licensing-card-container-top">
          <div className="licensing-tag">
            <div className="licensing-tag-tier">Tier One tag; 20 char</div>
          </div>
        </div>
        <div className="licensing-card-container-bottom">
          <div className="licensing-card-container-left">
            <div className="licensing-card-subhead">Cisco DNA</div>
            <div className="licensing-card-head">Essentials</div>
            <p className="licensing-card-content">
              Recommmended for single data centers. Includes both NX-OS and ACI
              management and operations capabilities, offering flexibility to
              migrate as needed.
            </p>
          </div>
          <div className="licensing-card-container-right">
            <div className="licensing-card-price">
              <div className="price">
                <div className="price-divider"></div>
                <div className="price-container">
                  <div className="price-dollar">
                    <img src={Dollar} alt="dollar" />
                  </div>
                  <div className="price-integer">14</div>
                  <div className="price-right">
                    <div className="price-decimal">95</div>
                    <div className="price-period">/month</div>
                  </div>
                </div>
              </div>
              <button className="price-btn">Get Started</button>
            </div>
          </div>
        </div>
      </div>
    );
  } else if (isMobile) {
    display = (
      <div className="licensing-card-container">
        <div className="licensing-tag">
          <div className="licensing-tag-tier">Tier One tag; 20 char</div>
        </div>
        <div className="licensing-card-subhead">Cisco DNA</div>
        <div className="licensing-card-head">Essentials</div>
        <div className="licensing-card-price">
          <div className="price">
            <div className="price-container">
              <div className="price-dollar">
                <img src={Dollar} alt="dollar" />
              </div>
              <div className="price-integer">14</div>
              <div className="price-right">
                <div className="price-decimal">95</div>
                <div className="price-period">/month</div>
              </div>
            </div>
          </div>
          <button className="price-btn">Get Started</button>
        </div>
        <p className="licensing-card-content">
          Recommmended for single data centers. Includes both NX-OS and ACI
          management and operations capabilities, offering flexibility to
          migrate as needed.
        </p>
      </div>
    );
  }

  return <Fragment>{display}</Fragment>;
}

export default LicensingCardTwo;



