import React, { useState, Fragment } from "react";
import logo from "../../icons/navglobal/logo.svg";
import search from "../../icons/navglobal/search.svg";
import hamburger from "../../icons/navglobal/hamburger.svg";
import closeToolTip from "../../icons/X/icLG-close.svg";
import GreaterThan from "../../icons/navglobal/greater_than.svg";
import language from "../../icons/navglobal/language.svg";
import "../../styles/navglobal.css";

function NavAccordion() {
  const [isActive, setIsActive] = useState(false);

  return (
    <div className="nav-accordion">
      <Fragment>
        {!isActive ? (
          <div className="nav-accordion-title">
            <img className="nav-accordion-logo" src={logo} alt="logo" />
            <span className="nav-accordion-title-right-icons">
              <img src={search} alt="search" />
              <img
                src={hamburger}
                alt="hamburger"
                onClick={() => setIsActive(!isActive)}
              />
            </span>
          </div>
        ) : (
          <img
            className="nav-accordion-clese"
            src={closeToolTip}
            alt="close"
            onClick={() => setIsActive(!isActive)}
          />
        )}
      </Fragment>
      {isActive && (
        <div className="nav-accordion-menu">
          <div className="menu-item">
            <p>Products</p>
            <img src={GreaterThan} alt="Greater Than" />
          </div>
          <div className="menu-item">
            <p>Solutions</p>
            <img src={GreaterThan} alt="Greater Than" />
          </div>
          <div className="menu-item">
            <p>Support</p>
            <img src={GreaterThan} alt="Greater Than" />
          </div>
          <div className="menu-item">
            <p>Learn</p>
            <img src={GreaterThan} alt="Greater Than" />
          </div>

          <div className="menu-link">View Link</div>

          <div className="menu-item-lower">How to buy</div>
          <div className="menu-item-lower">Partners</div>
          <div className="menu-item-lower">
            <span className="menu-lower-image">
              <img src={language} alt="language" />
            </span>
            <span>EN US</span>
          </div>

          <div className="menu-button-container">
            <button className="menu-button">Log in</button>
          </div>
        </div>
      )}
    </div>
  );
}

export default NavAccordion;



