import React, {
  useState,
  useCallback,
  useEffect,
  useRef,
  Fragment,
} from "react";
import "../styles/licensing.css";
import GreaterThan from "../icons/12/greater_than.svg";
import LicensingCardOne from "./licensing/LicensingCardOne";
import LicensingCardTwo from "./licensing/LicensingCardTwo";
import LicensingCardThree from "./licensing/LicensingCardThree";
import LicensingCardFour from "./licensing/LicensingCardFour";
import UseFadeIn from "./features/UseFadeIn";

const useMediaQuery = (width) => {
  const [targetReached, setTargetReached] = useState(false);

  const updateTarget = useCallback((e) => {
    if (e.matches) {
      setTargetReached(true);
    } else {
      setTargetReached(false);
    }
  }, []);

  useEffect(() => {
    const media = window.matchMedia(`(min-width: ${width}px)`);
    media.addEventListener("change", (e) => updateTarget(e));

    // Check on mount (callback is not called until a change occurs)
    if (media.matches) {
      setTargetReached(true);
    }

    return () => media.removeEventListener("change", (e) => updateTarget(e));
  }, [updateTarget, width]);

  return targetReached;
};

function Licensing() {
  const isDesktop = useMediaQuery(1440);
  const isTablet = useMediaQuery(768);
  const isMobile = useMediaQuery(320);

  const { isVisible, element } = UseFadeIn(180);

  //   const element = useRef(null);

  //   const handleScroll = () => {
  //     if (!element.current) return;
  //     let height = window.innerHeight - 180;
  //     if (element.current.getBoundingClientRect().top < height) {
  //       setShow(true);
  //     }
  //   };

  //   useEffect(() => {
  //     window.addEventListener("scroll", handleScroll);
  //     return () => {
  //       window.removeEventListener("scroll", () => handleScroll);
  //     };
  //   }, [handleScroll]);

  const { show, setShow } = useState({
    isHeadline: false,
    isCardOne: false,
    isCardTwo: false,
    isCardThree: false,
    isCardFour: false,
  });

  let display;
  if (isDesktop) {
    display = (
      <div className="licensing-container">
        <div className="licensing-top">
          <div ref={element} className="licensing-headline">
            Software subscription
          </div>
          <div className={isVisible ? "licensing-launch" : "not-visible"}>
            <div className="licensing-button">Help me choose</div>
            <div className="licensing-icon-greater">
              <img src={GreaterThan} alt="Greater Than" />
            </div>
          </div>
        </div>
        <div className={isVisible ? "licensing-middle" : "not-visible"}>
          <LicensingCardOne
            isDesktop={isDesktop}
            isTablet={isTablet}
            isMobile={isMobile}
          />
          <LicensingCardTwo
            isDesktop={isDesktop}
            isTablet={isTablet}
            isMobile={isMobile}
          />
          <LicensingCardThree
            isDesktop={isDesktop}
            isTablet={isTablet}
            isMobile={isMobile}
          />
          <LicensingCardFour
            isDesktop={isDesktop}
            isTablet={isTablet}
            isMobile={isMobile}
          />
        </div>
        <div className={isVisible ? "licensing-bottom" : "not-visible"}>
          <button className="licencing-btn">Compare all plans</button>
        </div>
      </div>
    );
  } else if (isTablet || isMobile) {
    display = (
      <div className="licensing-container">
        <div className="licensing-top">
          <div ref={element} className="licensing-headline">
            Software subscription
          </div>
          <div className={isVisible ? "licensing-launch" : "not-visible"}>
            <div className="licensing-button">Help me choose</div>
            <div className="licensing-icon-greater">
              <img src={GreaterThan} alt="Greater Than" />
            </div>
          </div>
        </div>
        <div className="licensing-middle">
          <Fragment
            ref={element}
            className={isVisible ? "is-visible" : "not-visible"}
          >
            <LicensingCardOne
              isDesktop={isDesktop}
              isTablet={isTablet}
              isMobile={isMobile}
            />
          </Fragment>
          <Fragment
            ref={element}
            className={isVisible ? "is-visible" : "not-visible"}
          >
            <LicensingCardTwo
              isDesktop={isDesktop}
              isTablet={isTablet}
              isMobile={isMobile}
            />
          </Fragment>
          <Fragment
            ref={element}
            className={isVisible ? "is-visible" : "not-visible"}
          >
            <LicensingCardThree
              isDesktop={isDesktop}
              isTablet={isTablet}
              isMobile={isMobile}
            />
          </Fragment>
          <Fragment
            ref={element}
            className={isVisible ? "is-visible" : "not-visible"}
          >
            <LicensingCardFour
              isDesktop={isDesktop}
              isTablet={isTablet}
              isMobile={isMobile}
            />
          </Fragment>
        </div>
        <div className={isVisible ? "licensing-bottom" : "not-visible"}>
          <button className="licencing-btn">Compare all plans</button>;
        </div>
      </div>
    );
  }

  return <Fragment>{display}</Fragment>;
}

export default Licensing;



