import React, { useState } from "react";
import "../../styles/navsecondary.css";
import dropdown from "../../icons/navsecondary/dropdown.svg";
import closeup from "../../icons/navsecondary/closeup.svg";

function NavSecAccordion({
  isSticky,
  element,
  isTablet,
  isMobile,
  handleClick,
}) {
  const [isActive, setIsActive] = useState(false);
  const tablet = isSticky && isTablet;
  const mobile = isSticky && isMobile && !isTablet;

  return (
    <div className="nav-secondary-container" ref={element}>
      <div className="nav-secondary-accordion">
        {!isActive ? (
          <div className="nav-secondary-accordion-upper">
            <div
              className="nav-secondary-accordion-upper-left"
              onClick={() => setIsActive(!isActive)}
            >
              <div className="nav-desktop-overview">Overview</div>
              <img
                className="nav-secondary-accordion-dropdown"
                src={dropdown}
                alt="dropdown"
              />
            </div>
            {tablet && (
              <div className="nav-secondary-accordion-upper-right">
                <button className="nav-secondary-button">
                  Request 1:1 demo
                </button>
              </div>
            )}
          </div>
        ) : (
          <div className="nav-secondary-accordion-upper">
            <div
              className="nav-secondary-accordion-upper-left"
              onClick={() => setIsActive(!isActive)}
            >
              <div className="nav-desktop-overview">Overview</div>
              <img
                className="nav-secondary-accordion-closeup"
                src={closeup}
                alt="closeup"
              />
            </div>
            {tablet && (
              <div className="nav-secondary-accordion-upper-right">
                <button className="nav-secondary-button">
                  Request 1:1 demo
                </button>
              </div>
            )}
          </div>
        )}
        {isActive && (
          <div className="nav-secondary-accordion-menu">
            <div
              className="nav-desktop-overview"
              id="1"
              onClick={(e) => handleClick(e)}
            >
              Overview
            </div>
            <div
              className="nav-desktop-item"
              id="2"
              onClick={(e) => handleClick(e)}
            >
              Resources
            </div>
            <div
              className="nav-desktop-item"
              id="3"
              onClick={(e) => handleClick(e)}
            >
              Models
            </div>
            <div
              className="nav-desktop-item"
              id="4"
              onClick={(e) => handleClick(e)}
            >
              Licenses
            </div>
            {mobile && (
              <div className="nav-secondary-accordion-menu-mobile-button">
                <button className="nav-secondary-button">
                  Request 1:1 demo
                </button>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}

export default NavSecAccordion;



