import React, { Fragment } from "react";
import "../../styles/features.css";
import FeatureAutoPlay from "./FeatureAutoPlay";

function FeatureAccordion({
  accordion,
  id,
  open,
  onClick,
  screen,
  isVisible,
  isClick,
  setIsClick,
}) {
  // const [isClick, setIsClick] = useState(false);

  const accordionClick = (id) => {
    onClick(id);
    setIsClick(true);
  };

  // useEffect(() => {
  //   const timer = setTimeout(() => {
  //     setIsClick(false);
  //   }, 5000);
  //   return () => clearTimeout(timer);
  // }, [isClick]);

  const titleStyle = open ? { color: "#1B1C1D" } : { color: "#939595" };
  const indicatorStyle = !open
    ? {
        marginLeft: "2px",
        // marginRight: "2px",
        minWidth: "2px",
        maxWidth: "2px",
      }
    : isClick
    ? {
        marginLeft: "0px",
        marginRight: "0px",
        minWidth: "4px",
        maxWidth: "4px",
      }
    : {
        marginLeft: "2px",
        // marginRight: "2px",
        minWidth: "2px",
        maxWidth: "2px",
        backgroundColor: "#939595",
      };

  const cardStyle = open
    ? { overflow: "hidden", maxHeight: "400px" }
    : { maxHeight: "32px" };

  return (
    <Fragment className="Feature-sub-container">
      {open && screen && (
        <img className="carousel-image" src={accordion.image} alt="carousel" />
      )}
      <div
        className="Feature-Accodion-container"
        // onMouseOver={() => setIsPlay(false)}
        // onMouseOut={() => setIsPlay(true)}
      >
        <div className="Feature-Accordion-indicator" style={indicatorStyle}>
          {isClick === false && open && (
            <FeatureAutoPlay height={100} isVisible={isVisible} />
          )}
        </div>
        <div
          style={cardStyle}
          className="Feature-Accorddion-card"
          onClick={() => accordionClick(id)}
        >
          <div className="Feature-Accordion-title" style={titleStyle}>
            {accordion.title}
          </div>
          {open && (
            <div className="Feature-Accordion-content">{accordion.content}</div>
          )}
        </div>
      </div>
    </Fragment>
  );
}

export default FeatureAccordion;



