import React, { useState } from "react";
import Plus from "../../icons/plus/plus.svg";
import Minus from "../../icons/minus/minus.svg";
// import styled from "styled-components";

function Accordion({ title, content }) {
  const [isActive, setIsActive] = useState(false);
  const fontStyle = isActive
    ? { fontFamily: `"CiscoSansTT-Medium", sans-serif` }
    : { fontFamily: `"CiscoSansTTLight", sans-serif` };

  return (
    <div className="accordion">
      <div className="accordion-title" onClick={() => setIsActive(!isActive)}>
        <span style={fontStyle}>{title}</span>
        <span>
          {isActive ? (
            <img src={Minus} alt="minus" />
          ) : (
            <img src={Plus} alt="plus" />
          )}
        </span>
      </div>
      {isActive && (
        <div className="accordion-content">
          {content.map((item, index) => {
            return (
              <p className="content-item" key={index}>
                {item}
              </p>
            );
          })}
        </div>
      )}
    </div>
  );
}

export default Accordion;



