import React, { useState, useCallback, useEffect, Fragment } from "react";
import "../styles/navglobal.css";
import language from "../icons/navglobal/language.svg";
import logo from "../icons/navglobal/logo.svg";
import search from "../icons/navglobal/search.svg";
import NavAccordion from "./navGlobal/NavAccordion";

const useMediaQuery = (width) => {
  const [targetReached, setTargetReached] = useState(false);

  const updateTarget = useCallback((e) => {
    if (e.matches) {
      setTargetReached(true);
    } else {
      setTargetReached(false);
    }
  }, []);

  useEffect(() => {
    const media = window.matchMedia(`(min-width: ${width}px)`);
    media.addEventListener("change", (e) => updateTarget(e));

    // Check on mount (callback is not called until a change occurs)
    if (media.matches) {
      setTargetReached(true);
    }

    return () => media.removeEventListener("change", (e) => updateTarget(e));
  }, [updateTarget, width]);

  return targetReached;
};

function NavGlobal() {
  const isDesktopMD = useMediaQuery(1024);
  const isTablet = useMediaQuery(768);
  const isMobile = useMediaQuery(320);

  let display;

  if (isDesktopMD) {
    display = (
      <div className="navglobal-container">
        <span className="navglobal-upper">
          <div className="upper-item">How to buy</div>
          <div className="upper-item">Partners</div>
          <div className="upper-item">Log in</div>
          <div className="upper-item">
            <span>EN US</span>
            <span className="upper-image">
              <img src={language} alt="language" />
            </span>
          </div>
        </span>
        <div className="navglobal-lower">
          <span className="lower-left">
            <span>
              <img src={logo} alt="logo" />
            </span>
            <span className="lower-item">Products</span>
            <span className="lower-item">Solutions</span>
            <span className="lower-item">Support</span>
            <span className="lower-item"> Learn</span>
          </span>
          <span className="lower-right">
            <span className="lower-right-text">Explore Cisco</span>
            <span>
              <img src={search} alt="search" />
            </span>
          </span>
        </div>
      </div>
    );
  } else if (isTablet || isMobile) {
    display = (
      <div className="navglobal-container">
        <NavAccordion />
      </div>
    );
  }

  return <Fragment>{display}</Fragment>;
}

export default NavGlobal;



