import React, { useState, useRef, useEffect } from "react";
import "../../styles/features.css";

function FeatureAutoPlay({ height, isVisible }) {
  //handle autoplay
  const [progress, setProgress] = useState({});
  const delay = 100;
  const timeoutRef = useRef(null);
  const resetTimeout = () => {
    if (!timeoutRef.current) return;
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
    }
  };

  useEffect(() => {
    if (isVisible) {
      resetTimeout();
      timeoutRef.current = setTimeout(() => {
        const newProgress = {
          opacity: 1,
          height: `${height}%`,
        };
        setProgress(newProgress);
      }, delay);

      return () => {
        resetTimeout();
      };
    } else {
      return;
    }
  }, [progress, isVisible]);

  return (
    <div className="Feature-Accordion-indicator-auto" style={progress}></div>
  );
}

export default FeatureAutoPlay;



