import React from "react";
import PropTypes from "prop-types";

const Header = ({ title }) => {
  return (
    <header className="service-header">
      <span>{title}</span>
    </header>
  );
};

Header.propTypes = {
  title: PropTypes.string.isRequired,
};

export default Header;



