import React, { useState, useCallback, useEffect, Fragment } from "react";
import NavSecAccordion from "./navSecondarry/NavSecAccordion";
import useSticky from "./navSecondarry/UseSticky";
import "../styles/navsecondary.css";

const useMediaQuery = (width) => {
  const [targetReached, setTargetReached] = useState(false);

  const updateTarget = useCallback((e) => {
    if (e.matches) {
      setTargetReached(true);
    } else {
      setTargetReached(false);
    }
  }, []);

  useEffect(() => {
    const media = window.matchMedia(`(min-width: ${width}px)`);
    media.addEventListener("change", (e) => updateTarget(e));

    // Check on mount (callback is not called until a change occurs)
    if (media.matches) {
      setTargetReached(true);
    }

    return () => media.removeEventListener("change", (e) => updateTarget(e));
  }, [updateTarget, width]);

  return targetReached;
};

function NavSecondary({ handleClick }) {
  const { isSticky, element } = useSticky();
  const [currentId, setCurrentId] = useState(0);

  const clickItem = (e) => {
    handleClick(e);
    setCurrentId(Number(e.target.id));
  };

  // console.log(currentId);

  const isDesktop = useMediaQuery(1440);
  const isDesktopMD = useMediaQuery(1024);
  const isTablet = useMediaQuery(768);
  const isMobile = useMediaQuery(320);

  let display;

  if (isDesktop || isDesktopMD) {
    display =
      isSticky !== true ? (
        <div className="nav-secondary-container" ref={element}>
          <div
            className="nav-desktop-overview"
            id="1"
            onClick={(e) => clickItem(e)}
          >
            Overview
          </div>
          <div
            id="2"
            onClick={(e) => clickItem(e)}
            className={
              currentId === 2 ? "nav-desktop-item-active" : "nav-desktop-item"
            }
          >
            Resources
          </div>
          <div
            className={
              currentId === 3 ? "nav-desktop-item-active" : "nav-desktop-item"
            }
            id="3"
            onClick={(e) => clickItem(e)}
          >
            Models
          </div>
          <div
            className={
              currentId === 4 ? "nav-desktop-item-active" : "nav-desktop-item"
            }
            id="4"
            onClick={(e) => clickItem(e)}
          >
            Licenses
          </div>
        </div>
      ) : (
        <div
          className="nav-secondary-container"
          style={{
            justifyContent: "space-between",
            paddingTop: "8px",
            paddingBottom: "8px",
          }}
          ref={element}
        >
          <div className="nav-secondary-container-left">
            <div
              className="nav-desktop-overview"
              id="1"
              onClick={(e) => clickItem(e)}
            >
              Overview
            </div>
            <div
              className={
                currentId === 2 ? "nav-desktop-item-active" : "nav-desktop-item"
              }
              id="2"
              onClick={(e) => clickItem(e)}
            >
              Resources
            </div>
            <div
              className={
                currentId === 3 ? "nav-desktop-item-active" : "nav-desktop-item"
              }
              id="3"
              onClick={(e) => clickItem(e)}
            >
              Models
            </div>
            <div
              className={
                currentId === 4 ? "nav-desktop-item-active" : "nav-desktop-item"
              }
              id="4"
              onClick={(e) => clickItem(e)}
            >
              Licenses
            </div>
          </div>
          <div className="nav-secondary-container-right">
            <button className="nav-secondary-button">Request 1:1 demo</button>
          </div>
        </div>
      );
  } else if (isTablet) {
    display = (
      <NavSecAccordion
        isSticky={isSticky}
        element={element}
        isTablet={isTablet}
        isMobile={isMobile}
        handleClick={handleClick}
      />
    );
  } else if (isMobile) {
    display = (
      <NavSecAccordion
        isSticky={isSticky}
        element={element}
        isTablet={isTablet}
        isMobile={isMobile}
        handleClick={handleClick}
      />
    );
  }

  return <Fragment>{display}</Fragment>;
}

export default NavSecondary;



