import React from "react";

const BundleContent = ({ paragraph_one, paragraph_two }) => {
  return (
    <div className="bundle-content">
      <p className="paragraph-one">{paragraph_one}</p>
      <p className="paragraph-two">{paragraph_two}</p>
    </div>
  );
};

export default BundleContent;



