import React from "react";
import "../../styles/footer.css";
import styled from "styled-components";
import Facebook from "../../icons/SoMe/Facebook.svg";
import Twitter from "../../icons/SoMe/Twitter.svg";
import LinkedIn from "../../icons/SoMe/LinkedIn.svg";
import Youtube from "../../icons/SoMe/Youtube.svg";
import Instagram from "../../icons/SoMe/Instagram.svg";

const Span = styled.span`
  margin-right: 32px;
`;

function SoMe() {
  return (
    <div className="soMe-icon">
      <Span>
        <img src={Facebook} alt="Facebook" />
      </Span>
      <Span>
        <img src={Twitter} alt="Twitter" />
      </Span>
      <Span>
        <img src={LinkedIn} alt="LinkedIn" />
      </Span>
      <Span>
        <img src={Youtube} alt="Youtube" />
      </Span>
      <span>
        <img src={Instagram} alt="Instagram" />
      </span>
    </div>
  );
}

export default SoMe;



