import React, { Fragment } from "react";
import "../../styles/licensing.css";
import Dollar from "../../icons/currency/USDollar.svg";

function LicensingCardOne({ isDesktop, isTablet, isMobile }) {
  let display;
  if (isDesktop) {
    display = (
      <div className="licensing-card-container">
        <div className="licensing-tag"></div>
        <div className="licensing-card-subhead">Cisco DNA</div>
        <div className="licensing-card-head">Basic</div>
        <div className="licensing-card-price">
          <div className="price">
            <div className="price-divider"></div>
            <div className="price-container">
              <div className="price-dollar">
                <img src={Dollar} alt="dollar" />
              </div>
              <div className="price-integer">0</div>
            </div>
            <button className="price-btn">Get Started</button>
          </div>
        </div>
        <p className="licensing-card-content">
          175 characters max, consectetur adipiscing elit, sed do eiusmod tempor
          incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,
          quis nostrud exercitation.
        </p>
      </div>
    );
  } else if (isTablet) {
    display = (
      <div className="licensing-card-container">
        <div className="licensing-card-container-top"></div>
        <div className="licensing-card-container-bottom">
          <div className="licensing-card-container-left">
            <div className="licensing-card-subhead">Cisco DNA</div>
            <div className="licensing-card-head">Basic</div>
            <p className="licensing-card-content">
              175 characters max, consectetur adipiscing elit, sed do eiusmod
              tempor incididunt ut labore et dolore magna aliqua. Ut enim ad
              minim veniam, quis nostrud exercitation.
            </p>
          </div>
          <div className="licensing-card-container-right">
            <div className="licensing-card-price">
              <div className="price">
                <div className="price-divider"></div>
                <div className="price-container">
                  <div className="price-dollar">
                    <img src={Dollar} alt="dollar" />
                  </div>
                  <div className="price-integer">0</div>
                </div>
                <button className="price-btn">Get Started</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  } else if (isMobile) {
    display = (
      <div className="licensing-card-container">
        <div className="licensing-card-subhead">Cisco DNA</div>
        <div className="licensing-card-head">Basic</div>
        <div className="licensing-card-price">
          <div className="price">
            <div className="price-container">
              <div className="price-dollar">
                <img src={Dollar} alt="dollar" />
              </div>
              <div className="price-integer">0</div>
            </div>
            <button className="price-btn">Get Started</button>
          </div>
        </div>
        <p className="licensing-card-content">
          175 characters max, consectetur adipiscing elit, sed do eiusmod tempor
          incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,
          quis nostrud exercitation.
        </p>
      </div>
    );
  }

  return <Fragment>{display}</Fragment>;
}

export default LicensingCardOne;



