import React from "react";
import CollabTitle from "./collab/CollabTitle";
import CollabCard from "./collab/CollabCard";
import CollabButton from "./collab/CollabButton";
import "../styles/collab.css";
// import FrameApple from "../icons/pictures/Frame_apple.png";
// import FrameDropbox from "../icons/pictures/Frame_dropbox.png";
// import FrameGoogle from "../icons/pictures/Frame_google.png";
// import FrameOutlook from "../icons/pictures/Frame_outlook.png";
// import FrameSlack from "../icons/pictures/Frame_slack.png";
import Android from "../icons/integrations/Android_logo.svg";
import Apple from "../icons/integrations/Apple_logo.svg";
import Linux from "../icons/integrations/Linux_logo.svg";
import Microsoft from "../icons/integrations/Microsoft_logo.svg";

const Collab = () => {
  const CONTENT =
    "Body copy paragraph 100 characters max; 35 character line limit lorem ipsum dolor sit amet, consectetur adipiscing";
  const VIEWOPTION = "Learn more";
  return (
    <div className="collab-container">
      <div className="collab-container-sub">
        <CollabTitle title="Forge stronger security with Cisco integrations" />
        <div className="collab-content">
          <CollabCard
            subtitle="Cisco Umbrella"
            content={CONTENT}
            viewOption={VIEWOPTION}
          />
          <CollabCard
            subtitle="Cisco Duo"
            content={CONTENT}
            viewOption={VIEWOPTION}
          />
          <CollabCard
            subtitle="Cisco Secure Firewall Malware Defense"
            content={CONTENT}
            viewOption={VIEWOPTION}
          />
        </div>
      </div>

      <div className="collab-icons">
        {/* <img className="collab-icon" src={FrameOutlook} alt="Outlook Icons" />
        <img className="collab-icon" src={FrameGoogle} alt="Google Icons" />
        <img className="collab-icon" src={FrameDropbox} alt="Dropbox Icons" />
        <img className="collab-icon" src={FrameApple} alt="Apple Icons" />
        <img className="collab-icon" src={FrameSlack} alt="Slack Icons" /> */}
        <img className="integration-icon" src={Android} alt="Android" />
        <img className="integration-icon" src={Apple} alt="Apple" />
        <img className="integration-icon" src={Linux} alt="Linux" />
        <img className="integration-icon" src={Microsoft} alt="Microsoft" />
      </div>
      <CollabButton />
    </div>
  );
};

export default Collab;



