import React from "react";
import icMD from "../../icons/info/icMD.svg";
import Star from "./Stars";
import Popup from "reactjs-popup";
import closeToolTip from "../../icons/X/icLG-close.svg";

function Rattings({ review, isDesktopXL, isDesktop, isTablet, isMobile }) {
  const rate = 4.5;

  const contentStyle = {
    background: "white",
    width: "90%",
  };

  const overlayStyle = { background: "rgba(255,255,255,0.9)" };

  const display =
    isDesktopXL === true || isDesktop === true ? (
      <Popup
        trigger={() => <img src={icMD} alt="info" className="icMD" />}
        position="bottom center"
        closeOnDocumentClick
      >
        <p className="tooltip">
          Tooltip copy 120 characters Lorem ipsum dolor sit amet, consectetur
          adipiscing elit, sed do eiusmod tempor incididuntm
        </p>
      </Popup>
    ) : isTablet === true ? (
      <Popup
        trigger={() => <img src={icMD} alt="info" className="icMD" />}
        position="bottom right"
        closeOnDocumentClick
      >
        <p className="tooltip">
          Tooltip copy 120 characters Lorem ipsum dolor sit amet, consectetur
          adipiscing elit, sed do eiusmod tempor incididuntm
        </p>
      </Popup>
    ) : (
      <Popup
        trigger={() => <img src={icMD} alt="info" className="icMD" />}
        modal
        contentStyle={contentStyle}
        overlayStyle={overlayStyle}
      >
        {(close) => (
          <div className="modal">
            <a className="close" onClick={close}>
              {/* &times; */}
              <img src={closeToolTip} alt="closeToolTip" />
            </a>
            <p className="tooltip">
              Tooltip copy 120 characters inkh Lorem ipsum dolor sit amet,
              consectetur adipiscing elit, sed do eiusmod tempor incididuntm
            </p>
          </div>
        )}
      </Popup>
    );
  return (
    <div className="ratings-container">
      <div className="star-container">
        <p className="rate">{rate}</p>
        <Star rate={rate} />
      </div>
      <div className="reviews-container">
        <div className="reviews">based on {review} reviews</div>
        {display}
      </div>
    </div>
  );
}

export default Rattings;



